var latitute = $('#lat').val();
var lngitute = $('#lng').val();
if(latitute != '' && lngitute != ''){
    var lat = Number(latitute);
    var lng = Number(lngitute);
}else{
    var lat = 35.837850811893915;
    var lng = 50.945327148437514;
}



var map, infoWindow;
var oldMarker;


function initMap() {

    var map = new google.maps.Map(document.getElementById('map-canvas'),
        {
            center : {
//                                lat : 35.837850811893915,
//                                lng : 50.945327148437514
                lat : lat,
                lng : lng
            },
            zoom : 12
        });



    if(latitute != '' && lngitute != '') {
        console.log('ok');

        var marker = new google.maps.Marker({
            position: {
                //                        lat : 35.837850811893915,
                //                        lng : 50.945327148437514
                lat: lat,
                lng: lng
            },
            map: map,
            draggable: true
        });

        var searchBox = new google.maps.places.SearchBox(document.getElementById('searchmap'));

        google.maps.event.addListener(searchBox, 'places_changed', function () {
            var places = searchBox.getPlaces();

            var bounds = new google.maps.LatLngBounds();
            var i, place;
            for (i = 0; place = places[i]; i++) {
                bounds.extend(place.geometry.location);
                marker.setPosition(place.geometry.location);
            }
            map.fitBounds(bounds);
            map.setZoom(12);
        });
        google.maps.event.addListener(marker, 'position_changed', function () {
            var lat = marker.getPosition().lat();
            var lng = marker.getPosition().lng();
            $('#lat').val(lat);
            $('#lng').val(lng);
        });
        google.maps.event.addListener(map, 'click', function(event) {
            placeMarker(event.latLng);
        });

        function placeMarker(location) {

            markerNew = new google.maps.Marker({
                position: location,
                map: map,
                animation: google.maps.Animation.DROP,
                draggable : true,
            });

            if (oldMarker != undefined){
                oldMarker.setMap(null);
            }
            if( marker != undefined){
                marker.setMap(null);
            }
            // oldMarker = marker;
            oldMarker = markerNew;
            map.setCenter(location);
            var lat = markerNew.getPosition().lat();
            var lng = markerNew.getPosition().lng();
            $('#lat').val(lat);
            $('#lng').val(lng);

            google.maps.event.addListener(markerNew, 'position_changed', function () {
                var lat = markerNew.getPosition().lat();
                var lng = markerNew.getPosition().lng();
                $('#lat').val(lat);
                $('#lng').val(lng);
            });


        }

    }else {
        console.log('no');

        infoWindow = new google.maps.InfoWindow;

        // Try HTML5 geolocation.
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(function(position) {
                var pos = {
                    lat: position.coords.latitude,
                    lng: position.coords.longitude
                };
                var marker = new google.maps.Marker({
                    position: {
                        lat : position.coords.latitude,
                        lng : position.coords.longitude
                    },
                    map: map,
                    draggable : true,
                    mapTypeId: google.maps.MapTypeId.ROADMAP,
                    streetViewControl: false,
                    mapTypeControl: false,
                });

                google.maps.event.addListener(marker, 'position_changed', function () {
                    var lat = marker.getPosition().lat();
                    var lng = marker.getPosition().lng();
                    $('#lat').val(lat);
                    $('#lng').val(lng);
                });


                /////////////////////////////////////
                google.maps.event.addListener(map, 'click', function(event) {
                    placeMarker(event.latLng);
                });

                function placeMarker(location) {

                    markerNew = new google.maps.Marker({
                        position: location,
                        map: map,
                        animation: google.maps.Animation.DROP,
                        draggable : true,
                    });

                    if (oldMarker != undefined){
                        oldMarker.setMap(null);
                    }
                    if( marker != undefined){
                        marker.setMap(null);
                    }
                    // oldMarker = marker;
                    oldMarker = markerNew;
                    map.setCenter(location);
                    var lat = markerNew.getPosition().lat();
                    var lng = markerNew.getPosition().lng();
                    $('#lat').val(lat);
                    $('#lng').val(lng);

                    google.maps.event.addListener(markerNew, 'position_changed', function () {
                        var lat = markerNew.getPosition().lat();
                        var lng = markerNew.getPosition().lng();
                        $('#lat').val(lat);
                        $('#lng').val(lng);
                    });


                }

                // google.maps.event.addListener(map, 'click', function(event) {
                //     placeMarker(event.latLng);
                // });
                //
                // function placeMarker(location) {
                //
                //     var markerNew = new google.maps.Marker({
                //         position: location,
                //         map: map,
                //         animation: google.maps.Animation.DROP,
                //         draggable : true
                //     });
                //     map.setCenter(location);
                //
                //     google.maps.event.addListener(markerNew, 'position_changed', function () {
                //         var lat = markerNew.getPosition().lat();
                //         var lng = markerNew.getPosition().lng();
                //         $('#lat').val(lat);
                //         $('#lng').val(lng);
                //     });
                //
                // }



                //////////////////////////////////////

                infoWindow.setPosition(pos);
                infoWindow.setContent('Location found.');
                infoWindow.open(map);
                map.setCenter(pos);
            }, function() {
                handleLocationError(true, infoWindow, map.getCenter());
            });
        } else {
            // Browser doesn't support Geolocation
            handleLocationError(false, infoWindow, map.getCenter());
        }

    }

}
